.loader {
  width: 106px;
  height: 56px;
  box-sizing: border-box;
  background-image: linear-gradient(#fff 50px, #0000 0), linear-gradient(#fff 50px, #0000 0), linear-gradient(#fff 50px, #0000 0), linear-gradient(#fff 50px, #0000 0), radial-gradient(14px, #fff 100%, #0000 0);
  background-position: 25px 5px, 58px 20px, 25px 17px, 2px 37px, 76px 0;
  background-repeat: no-repeat;
  background-size: 48px 15px, 15px 35px, 15px 35px, 25px 15px, 28px 28px;
  margin: 30px auto;
  display: block;
  position: relative;
  transform: rotate(-45deg);
}

.loader:after, .loader:before {
  content: "";
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  background-image: linear-gradient(#fff 64px, #0000 0), linear-gradient(#fff 66px, #0000 0), radial-gradient(4px, #fff 100%, #0000 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40px 1px, 1px 40px, 8px 8px;
  border: 6px solid #fff;
  border-radius: 50%;
  animation: rotation .3s linear infinite;
  position: absolute;
  top: -10px;
  left: -45px;
}

.loader:before {
  top: 60px;
  left: 25px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

